#page-wrapper,
#main-wrapper {
    background-color: #fff;
}

#main-content {
    min-height: 70vh;
}

th.ui-state-default {
    text-align: center;
    padding-top: 10px;
}

#table-content tr td {
    text-align: center;
}

#table-content input {
    margin: 0.5em 0;
    width: 90%;
    color: #2e2e2e;
    text-align: right;
    max-width: 120px;
}

#table-content td span {
    color: #616161;
}

tr.even {
    background-color: #F5F5F5 !important;
}

tr.odd {
    background-color: #e6e5e5 !important;
}

#table-content tr td:first-child {
    border-left: 1px solid #c5dbec !important;
}

#table-content tr td:last-child {
    border-right: 1px solid #c5dbec !important;
}

#table-content tr:last-child td {
    border-bottom: 1px solid #c5dbec !important;
}

.table-content {
    min-width: 800px;
    max-width: 1000px;
    flex-grow: 1;
}

#tabelaDefault_wrapper {
    display: flex;
    margin-bottom: 100px;
}

#grafico {
    flex: 1;
    max-width: 1000px;
}

.box-0 {
    position: relative;
    width: 300px;
    height: 450px;
    margin-right: 20px;
}

.box-1 {
    background-color: #3d4297e3;
    border-radius: 0.5rem;
    width: 100%;
    height: 100%;
    position: absolute;
    top: -5px;
    left: -5px;
}

.box-2 {
    border-radius: 0.5rem;
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    padding: 2rem;
}

.gone {
    visibility: hidden;
}

.text-button {
    font-weight: bold;
    font-size: 1.2em;
}


/* General */

.column-fixed .form-container {
    margin: 0 1em 1em 0;
}

.form-container {
    background: #f8f8f8 !important;
    margin: 0;
}

#form ul {
    margin: 0;
    padding: 0;
    list-style-type: none;
    width: 100%;
}

#form li {
    display: block;
    margin: 0;
    padding: 6px 0 9px;
    clear: both;
}

#form li:after,
form li div:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

* html form li {
    height: 1%;
}

* html form li div {
    display: inline-block;
}

*+html form li div {
    display: inline-block;
}

#form li div,
form li span {
    margin: 0 .3em 0 0;
    padding: 0;
    color: #444;
}

#form li span {
    float: left;
}

/* Inputs */

.parceiro-pontos {
    width: 12em;
    margin-top: 0.5em;
}

input.text,
input.file,
textarea.textarea,
select.select {
    color: #333;
    margin: 4px 0 10px 0;
    padding: 4px;
}

input.text,
textarea.textarea {
    border-top: 1px solid #7c7c7c;
    border-left: 1px solid #c3c3c3;
    border-right: 1px solid #c3c3c3;
    border-bottom: 1px solid #ddd;
}

.table-dados-reserva input {
    border: 0px;
    float: right;
    text-align: right;
    background-color: #f2f9fe;
}

.table-dados-reserva .outro-valor {
    background-color: #ffffff;
}

.table-dados-reserva {
    margin-top: 10px;
    padding: 5px;
    background-color: #f2f9fe;
    border: 1px solid #a6c9e2;
    border-radius: 5px
}

.table-dados-reserva tbody tr td {
    border-bottom: dotted 1px #a6c9e2
}

input.checkbox,
input.radio {
    display: block;
    margin: 4px 0 0 0;
    padding: 0;
    width: 13px;
    height: 13px;
}

input.other {
    margin: 0 0 8px 25px;
}

.safari select.select {
    font-size: 120% !important;
    margin: 0 0 1px 0;
}

* html select.select {
    margin: 1px 0;
}

*+html select.select {
    margin: 1px 0;
    border: 1px solid red;
}

/* Size */

input.small,
select.small {
    width: 25%;
}

input.medium,
select.medium {
    width: 50%;
}

input.large,
select.large,
textarea.textarea {
    width: 70%;
}

input.full,
select.full,
textarea.full {
    width: 99%;
}

textarea.small {
    height: 5.5em;
}

textarea.medium {
    height: 10em;
}

textarea.large {
    height: 20em;
}

/* Buttons */

input.btTxt {
    padding: 0 7px;
    width: auto;
    overflow: visible;
}

#form li.buttons {
    clear: both;
    padding-top: 10px;
}

#form li.buttons input {
    font-size: 120%;
    margin-right: 5px;
}

/* Labels */

#form li div label,
form li span label {
    margin: 0;
    padding-top: 6px;
    clear: both;
    font-size: 85%;
    line-height: 100%;
    color: #444;
    display: block;
}

#form li div span label {
    padding-bottom: 12px;
}

label.desc {
    font-size: 95%;
    font-weight: bold;
    color: #222;
    line-height: 150%;
    margin: 0;
    padding: 0 0 3px 0;
    border: none;
    display: block;
}

label.choice {
    display: block;
    font-size: 100%;
    line-height: 150%;
    margin: -17px 0 0 23px;
    padding: 0 0 5px 0;
    color: #222;
    width: 88%;
}

.info-retencao {
    display: none;
    margin-top: 10px;
    padding: 5px;
    background-color: #f2f9fe;
    border: 1px solid #a6c9e2;
    border-radius: 5px
}

/* Required */

#form span.req {
    display: inline;
    float: none;
    color: red !important;
    font-weight: bold;
}

.clonedInput {
    margin: 0 0 5px 0;
}

#copyDown {
    top: 6px;
}

#btnAdd,
#btnDel,
#copyDown {
    padding: 10px 14px !important;
}

#btnAdd span.ui-button-text,
#btnDel span.ui-button-text,
#copyDown span.ui-button-text {
    display: none;
}

tr.ndown td a {
    display: none;
}

.action_plus {
    margin: 5px 0;
}

.action_buttons {
    margin: 20px 0 10px;
    float: left;
    width: 80%;
}

.action_buttons_out {
    margin: 20px 0 0;
    padding: 6px 0;
    float: right;
    width: 15%;
    text-align: right;
}

.adLateral>p {
    width: 95px;
}

/*width:95px; margin: 100px 15px 0 0; padding: 10px 5px; text-align: center; background:#fff; border-radius: 8px; -moz-border-radius:8px; -webkit-border-radius: 8px; float:right; }*/

#customers td,
#customers th {
    border: 1px solid darkgray;
    padding: 5px;
    color: black;
    text-align: left;
}

.select_readonly {
    background: #eee;
    /*Simular campo inativo */
    pointer-events: none;
    touch-action: none;
}

/* Login Page */
#wallpaper div#page-wrapper {
    background-image: url(/imagens/map.jpg);
    background-repeat: no-repeat;
    background-position: center 100%;
}

.login {
    height: 500px;
    padding-top: 100px;
}

.login div#log_access {
    margin: 5% 40% !important;
    background-color: rgba(131, 157, 192, 0.5);
    border: solid 5px rgba(255, 255, 255, 0.85);
    text-align: center;
    padding: 13px !important;
    border-radius: 4px;
}

.login div#log_access div.other {
    border: none !important;
    padding: 0 !important;
    margin: 0 !important;
}

.login div#log_access h1 {
    color: #FFF;
    font-size: 16px !important;
    text-shadow: none !important;
    padding: 5px 0 !important;
    margin: 0;
    text-align: left;
    text-transform: uppercase;
}

.login div#log_access .login_error {
    color: #000 !important;
    font-weight: bold;
    text-align: left;
}

.login div#log_access ul li {
    margin: 10px 0;
    display: block;
    width: 170px;
    margin: 4px 0 0 0;
    color: #FFF;
    text-align: left;
}

.login div#log_access ul li label {
    margin: 5px 0;
}

.login div#main-wrapper div#main-content {
    margin-right: 0 !important;
}

.login div#log_access ul li input.error {
    background-color: #FFF;
}

/* Layout helpers */

.ui-helper-hidden {
    display: none;
}

.ui-helper-hidden-accessible {
    position: absolute;
    left: -99999999px;
}

.ui-helper-reset {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    line-height: 1.3;
    text-decoration: none;
    font-size: 100%;
    list-style: none;
}

.ui-helper-clearfix:after {
    content: ".";
    display: block;
    height: 0;
    clear: both;
    visibility: hidden;
}

.ui-helper-clearfix {
    display: block;
}

* html .ui-helper-clearfix {
    height: 1%;
}

.ui-helper-zfix {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    position: absolute;
    opacity: 0;
    filter: Alpha(Opacity=0);
}

.fixed {
    /* Fixed width class - added to transform layout from fluid to fixed. */
    width: 1300px;
    margin: 0 auto;
}

.fixed #sidebar {
    border-width: 1px;
    margin: 25px 0 0 -242px !important;
}

/* Interaction Cues */

.ui-state-disabled {
    cursor: default !important;
}

/* States and /imagens */

.ui-icon {
    display: block;
    text-indent: -99999px;
    overflow: hidden;
    background-repeat: no-repeat;
    width: 16px;
    height: 16px;
}

